import React from "react"
import {Link} from "gatsby";

const Navigation = () => {
    return (
        <nav className="navbar">
            <ul className="">
                <li className="">
                    <Link to="/" className="">
                        Home
                    </Link>
                </li>
                <li className="">
                    <Link to="/about" className="">
                        About
                    </Link>
                </li>
            </ul>
        </nav>
    )
}

export default Navigation;